import React, { Component } from 'react'
import PropTypes from 'prop-types'
import { connect } from 'react-redux'

import Well from './Well'
import InfoPanel from './InfoPanel'

import {
  gameInit, gamePause, gameResume,
  moveLeft, moveRight, enableAccelerate, disableAccelerate, rotate,
  jumpDown, moveDown, gameStart, shift, newGame,
} from '../actions'
import { PLAYING } from '../constants/gameStatus'
import { UP, LEFT, RIGHT, DOWN } from '../constants/options'

import Tetromino from './Tetromino'

import { NEXT_TETRO_COLOR, SHAPES, TETROMINOS, COLORS } from '../constants/tetromino'

import './styles/TetrisGame.css'

let map = {}
// export common class component for test
export class TetrisGame extends Component {
  constructor() {
    super()

    this._onkeydown = this._onkeydown.bind(this)
    this._onkeyup = this._onkeyup.bind(this)
  }
  componentDidMount() {
    window.addEventListener('keydown', this._onkeydown)
    window.addEventListener('keyup', this._onkeyup)

    const { onGameInit } = this.props
    onGameInit()
  }

  componentWillUnmount() {
    window.removeEventListener('keydown', this._onkeydown)
    window.removeEventListener('keyup', this._onkeyup)
  }

  _getTetrominoProps() {
    const { hold, nextTetromino } = this.props
    return {
      color: COLORS[hold],
      tetroGrid: SHAPES[hold],
      isNextTetromino: true
    }
  }

  _onkeydown(e) {
    map[e.keyCode] = true
    e.preventDefault()
    const {
      onMoveLeft,
      onMoveRight,
      onRotate,
      onEnableAccelerate,
      isPlaying,
      isAccelerating,
      onJumpDown,
      onMoveDown,
      onGameResume,
      onGamePause,
      gameStatus,
      onHold,
    } = this.props
    
    if (map[82] && map[91]) {
      location.reload()
    }
    if (!isPlaying && e.keyCode === 80 && gameStatus !== "STOPPED") {
      onGameResume()
    }
    if (!isPlaying) return
    switch (e.keyCode) {
      case 16:
        if (isPlaying) {
          onHold()
        }
        break
      case 80:
        if (isPlaying) {
          onGamePause()
        }
        break
      case 32:
        onJumpDown()
        break
      case UP:
        onRotate()
        break
      case LEFT:
        onMoveLeft()
        break
      case RIGHT:
        onMoveRight()
        break
      case 40:
        onMoveDown()
        break
      default:
        return
    }
  }

  _onkeyup(e) {
    const { isPlaying, onDisableAccelerate } = this.props
    delete map[e.keyCode]
    if (!isPlaying) return
    if (e.keyCode === DOWN) {
      onDisableAccelerate()
    }
  }

  _getInfoPanelProps() {
    const { score, linesCleared, nextTetromino, gameStatus,hold, last, gameOver} = this.props
    return {
      score,
      linesCleared,
      nextTetromino,
      gameStatus,
      hold,
      last,
      gameOver,
    }
  }

  render() {
    const { score, gameOver } = this.props;
    return (
      <div>
      <div className="gameovertitle">{gameOver ? <div><h1>GAME OVER</h1><div className="hit-start">HIT START TO PLAY AGAIN!</div></div>: null}</div>
      <div className="game">
      <div className="controls-block"><h3>CONTROLS:</h3>
      <ul>
        <li>P = PAUSE/RESUME</li>
        <li>SPACE = DROP</li>
        <li>SHIFT = HOLD/SWAP</li>
      </ul>
      <div className="creator-title"><h3>
              TETRIS BY: <br />ALEXANDER CHUNG</h3>
      </div>
      </div>
          <div className="hold-tetro">
            HOLD
            <div className="center-tetro">
              <div>
                <Tetromino {...this._getTetrominoProps()} />
              </div>
            </div>
          </div>
          <div className="well">
            <Well />
          </div>
          <div className="next-tetro-panel">
            <InfoPanel {...this._getInfoPanelProps()} />
          </div>
        </div>
        <div className="score">
          <div>
            <span>
              <div>Your Score: <div className="score-num">{score}</div></div>
            </span>
          </div>
        </div>
      </div>
    )
  }
}

function mapStateToProps(state, ownProps) {
  return {
    gameStatus: state.gameStatus,
    score: state.score,
    linesCleared: state.linesCleared,
    nextTetromino: state.nextTetromino,
    isPlaying: state.gameStatus === PLAYING,
    isAccelerating: state.isAccelerating,
    hold: state.hold,
    last: state.last,
    gameOver: state.gameOver,
  }
}

function mapDispatchToProps(dispatch) {
  return {
    onGameInit: () => dispatch(gameInit()),
    onGamePause: () => dispatch(gamePause()),
    onGameResume: () => dispatch(gameResume()),
    onMoveLeft: () => dispatch(moveLeft()),
    onMoveRight: () => dispatch(moveRight()),
    onMoveDown: () => dispatch(moveDown()),
    onJumpDown: () => dispatch(jumpDown()),
    onRotate: () => dispatch(rotate()),
    onHold: () => dispatch(shift()),
    onEnableAccelerate: () => dispatch(enableAccelerate()),
    onDisableAccelerate: () => dispatch(disableAccelerate())
  }
}

TetrisGame.PropTypes = {
  gameStatus: PropTypes.string,
  isAccelerating: PropTypes.bool,
  isPlaying: PropTypes.bool,
  linesCleared: PropTypes.number,
  nextTetromino: PropTypes.oneOf(TETROMINOS),
  score: PropTypes.number,
  onJumpDown: PropTypes.func,
  onDisableAccelerate: PropTypes.func,
  onEnableAccelerate: PropTypes.func,
  onGameInit: PropTypes.func,
  onGamePause: PropTypes.func,
  onGameResume: PropTypes.func,
  onHold: PropTypes.func,
  onMoveLeft: PropTypes.func,
  onMoveRight: PropTypes.func,
  onRotate: PropTypes.func,
  hold: PropTypes.string,
  last: PropTypes.string,
  gameOver: PropTypes.bool
}

export default connect(mapStateToProps, mapDispatchToProps)(TetrisGame)



